@import (less) "color.less";

.tabs-container {
  display: flex;

  .tab-container {
    height: 87px;
    border-right: 1px solid @light_gray;
    text-align: center;
    background-color: @dark_white;
    flex-grow: 1;
    flex-basis: 0;
    padding: 10px;
    cursor: pointer;

    &:last-of-type {
      border: 0px;
    }

    &.selected {
      background-color: @white;
      height: 86px;

      i {
        color: @green;
      }

      .tab-title {
        color: @green;
      }
    }

    i {
      display: block;
      font-size: 24px;
      color: @gray;
    }

    .tab-title {
      text-transform: uppercase;
      color: @gray;
      font-size: 12px;
      display: inline-block;
      line-height: 1;
      font-weight: 600;
      margin-top: 10px;
      margin-bottom:10px;
    }
  }
}

